:root{
    --primary: #d81e5b;
    --secondary: #8c38ff;
    --dark: #131a26;
    --light: #eee
}
.app{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    padding: 16px;
}
.calculator{
    width: 100%;
    max-width: 400px;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0px 2px 64px rgba(0,0,0,0.2);
}
.display{
    padding: 16px;
    text-align: right;
    background-color: var(--dark);
    color: var(--light);
    font-size: 24px;
    font-weight: 300;
    span{
        font-size: 14px;
        color: #888;
    }
}
button{
    appearance: none;
    border: none;
    outline: none;
    color: #fff;
    font-size: 20px;
    padding: 16px;
    cursor: pointer;
    transition: 0.4s;
}
button:hover{
    opacity: 0.9;
}
.operators{
    display: flex;
    button{
        flex: 1 1 0%;
        background-color: var(--primary);
        font-weight: 700;
    }
}
.digits{
    display: flex;
    flex-wrap: wrap;
    button{
        flex: 1 1 33.333%;
        max-width: 33.333%;
        background-color: var(--dark);
    }
}